import React from 'react'
import {Breadcrumb} from 'antd'
import {Link} from 'react-router-dom'

const CustomBreadcrumb = (props)=>(
  <Breadcrumb style={{marginBottom:16,}}>
    <Breadcrumb.Item ><Link to='/home' style={{color:'white'}}></Link></Breadcrumb.Item>
    {props.arr && props.arr.map(item=>{
      if ((typeof item) === 'object'){
        return <Breadcrumb.Item key={item.title}><Link to={item.to} style={{color:'white'}}>{item.title}</Link></Breadcrumb.Item>
      } else {
        return <Breadcrumb.Item key={item} style={{color:'white'}}>{item}</Breadcrumb.Item>
      }
    })}
  </Breadcrumb>
)
export default CustomBreadcrumb


